* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: yellow;
  display:flex;
  flex-direction:column;
  align-items:center;
}

.app-container {
  min-height: 100dvh;
  height: 100vh;
  width: 100vw;
  position: relative;
  background:red;
}

.shapesIcon {
  position: absolute;
  top: -52px;
  left: -54px;
  width: 235px;
  height: 173px;
}

.shapesIcon1 {
  position: absolute;
  height: 20.5%;
  width: 60.26%;
  right: -18.97%;
  bottom: -4.74%;
}

.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
  justify-content: center;
  position: relative;
  padding: 0;
  background: blue;
  min-height: 100%;
  height: auto;
  display:flex;
  flex-direction:column;
  align-items:center;
}

:root {
  --pr-color: #32cd32;
  --sc-color: rgb(80, 128, 96);
}


@media (min-width: 1024px) {
  .app-container {
      width: 569px !important;
      margin: 0;
      background-color: lightblue;
      padding: 0;
      overflow:hidden;
      border:1px solid black;
  }
}